var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("绿魔")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        绿魔是几个虚构的别名的supervillains出现在美国漫画书的出版奇迹漫画。第一和最知名的化身，诺曼底Osborn，通过创建斯坦·李和史蒂夫·迪特科，通常被认为是大敌的蜘蛛侠。Green Goblin是一个以万圣节为主题的超级恶魔，其武器类似于蝙蝠，鬼魂和南瓜灯。漫画记者和历史学家Mike Conroy这个角色的写道：“多年来一直困扰着蜘蛛侠的所有被打扮的恶棍中，最让人眼花缭乱和恐怖的是绿色妖精。”")]),_c('blockquote',{staticClass:"blockquote"},[_vm._v("        具有里程碑意义的故事“ The Night Gwen Stacy Died ”（The Amazing Spider-Man＃121-122）中，Green Goblin杀死了Gwen Stacy，后来在与蜘蛛侠的斗争中灭亡。然而，故事的作家格里康威让哈里奥斯本在“夜间格温史黛西死后”中采用了绿妖精的身份，后来评论说“我从来没有打算将绿妖精摆脱为一个概念”。哈里·奥斯本成为绿色妖精的人大多受到欢迎，粉丝们说哈利比他父亲曾经更加威胁。\n\n其他几个角色将扮演绿妖精的身份，作家罗杰斯特恩后来介绍了大地精取代绿妖精作为蜘蛛侠的大敌。此外，“ 克隆传奇 ” 期间的一次侦察确定原始的绿妖精在“神奇蜘蛛侠”＃122 的事件中幸存下来，并且从那时起一直在蜘蛛侠的冒险中扮演幕后角色。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260","src":""}})],1),_c('br'),_c('h2',[_vm._v("创作背景")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('br'),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        Green Goblin在The Amazing Spider-Man＃14中首次亮相。此时他的身份不明，但他证明了自己很受欢迎，并在后来的问题中再次出现，这标志着他的秘密身份。根据Stan Lee和John Romita，他们取代Ditko作为头衔的艺术家，Lee一直希望Green Goblin成为Peter Parker所知道的人，而Ditko希望他的平民身份成为尚未被介绍的人。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        Lee详细说明：“史蒂夫希望他变成一个我们以前从未见过的角色。因为，他说，在现实生活中，一个坏人经常变成一个你从未认识的人。我觉得那个从某种意义上说，我觉得这就像欺骗读者一样......如果是你不认识和从未见过的人，那么关注所有线索有什么意义呢？我认为这会让人感到沮丧读者。” 然而，李先生承认这一说法，承认由于他自称的记忆力差，他可能会让绿妖精与另一个角色混淆，在早期的一篇文章中，他曾说过他不记得Norman Osborn是Green Goblin是他的想法还是Ditko的。Ditko坚持认为这是他的想法，甚至声称他已经在第一个Green Goblin故事结束之前做出了决定，并